.project-container {
    width: 100%;
    height: 100vh;
    text-align: center;
}

.project-container>h1 {
    font-size: 7rem;
    color: #00af83;
    margin: 3rem;
}

.project-container a {
    text-decoration: none;
}

@media screen and (max-width: 600px) {
    .project-container h1 {
        font-size: 2rem;
    }
}
.landing-page {
    text-align: center;
    height: 100%;
    position: relative;
}

.banner-text {
    width: 75%;
    height: 100%;
    margin: auto;
}

.banner-text h1 {
    font-size: 6rem;
    color: #00af83;
    margin: 0;
}
.banner-text h1 i {
    color: #00af83;
    font-size: 100px;
}

.banner-text h2 {
    padding: 1.8rem 0;
}

.banner-text hr.styler {
    border: 0;
    height: 1px;
    background: #333;
    background-image: -webkit-linear-gradient(left, #ccc, #333, #ccc);
    background-image: linear-gradient(to right, #ccc, #333, #ccc);
    margin: 1.7rem 0;
}

.skills-container {
    display: flex;
    flex-flow: row wrap;
    width: 100%;
    align-content: center;
    justify-content: space-evenly;
    margin: 1.8rem auto;
}

.skills-container .skill-title {
    font-family: 'Baron Neue';
    color: #00af83;
    width: 100%;
    text-transform: uppercase;
}

.skills-container .skill-cards {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 5px;
    box-sizing: border-box;
    flex-basis: 15%;
    min-width: 100px;
}

.skills-container .skill-cards:hover *{
    color: #00af83;
}

.skills-container .skill-cards i {
    font-size: 3em;
    -webkit-transition: all ease 300ms;
    transition: all ease 300ms;
}

.skills-container .skill-cards h3 {
    font-family: 'Baron Neue';
    font-size: 1.5em;
    margin: auto;
    -webkit-transition: all ease 300ms;
    transition: all ease 300ms;
}

.social-links {
    display: flex;
    flex-flow: row wrap;
    width: 50%;
    align-content: center;
    justify-content: space-between;
    margin: auto;
    padding: 3rem;
}

.social-links i {
    font-size: 5em;
    color: #00af83;
    -webkit-transition: all ease 300ms;
    transition: all ease 300ms;
}

.social-links i:hover {
    color: rgb(216, 216, 216);
}

.arrow.bounce a { 
    color: rgb(216, 216, 216); 
    text-decoration: none;
    -webkit-transition: color ease 300ms;
    transition: color ease 300ms;
}

.arrow.bounce a:hover { 
    color: #00af83; 
}

.arrow {
    text-align: center;
    margin: 8% 0;
}
.bounce {
    -webkit-animation: bounce 2s infinite;
    animation: bounce 2s infinite;
}
  
@-webkit-keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    40% {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
    }
    60% {
      -webkit-transform: translateY(-15px);
              transform: translateY(-15px);
    }
}
  
@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    40% {
      -webkit-transform: translateY(-30px);
              transform: translateY(-30px);
    }
    60% {
      -webkit-transform: translateY(-15px);
              transform: translateY(-15px);
    }
}


@media screen and (max-width: 768px) {
    .banner-text h1 {
        font-size: 2rem;
    }

    .banner-text h4 {
        font-size: 1.5rem;
    }

    .skills-container {
        margin: 1rem auto;
    }

    .skills-container .skill-cards i {
        font-size: 2rem;
    }

    .skills-container .skill-cards h3 {
        font-size: 1rem;
    }

    .social-links i {
        font-size: 3em;
        color: #00af83;
        -webkit-transition: all ease 300ms;
        transition: all ease 300ms;
    }
    .banner-text hr.styler {
        margin: 0;
    }
    .social-links {
        padding: 0
    }
}
.toggle-button {
    background: transparent;
    border: none;
    cursor: pointer;
}

.toggle-button:focus {
    outline:none;
}

.toggle-button i {
    font-size: 1.3rem;
}

.toggle-button i:hover {
    color: #00af83;
}
.toolbar {
    width: 100%;
    height: 60px;
    background: #111112;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #111112, #232526);  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-linear-gradient(left, #111112, #232526);
    background: linear-gradient(to right, #111112, #232526);
    z-index: 100;
}

.toolbar-logo {
    margin-left: 1rem;
}

.toolbar-nav {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}

.toolbar-nav-items ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.toolbar-nav-items li {
    padding: 0 0.5rem;
}

.toolbar-logo a {
    text-decoration: none;
    color: rgb(216, 216, 216);
    font-size: 2rem;
}

.toolbar-logo a:hover {
    color: #00af83;
}

.spacer {
    flex: 1 1;
}

.toolbar-nav-items a {
    color: rgb(216, 216, 216);
    text-decoration: none;
}

.toolbar-nav-items a:hover,
.toolbar-nav-items a:active {
    color: #00af83;
}

@media screen and (max-width: 768px) {
    .toolbar-nav-items {
        display: none;
    }
    .toolbar-logo a {
        display: none;
    }

}

@media screen and (min-width: 769px) {
    .toolbar-toggle-button {
        display: none;
    }
    
}


.side-drawer {
    height: 100%;
    background: rgb(235, 235, 235);
    box-shadow: 1px 0px 7px rgba(0,0,0,0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 65%;
    max-width: 400px;
    z-index: 200;
    overflow-x: hidden;
    padding-top: 60px;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.side-drawer.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.side-drawer ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li {
    padding: 0.5rem;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.side-drawer li:hover {
    background-color: #111112;
}

.side-drawer li:hover a {
    color: #00af83;
}

.side-drawer a {
    color: #111112;
    text-decoration: none;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}


@media (min-width: 769px) {
    .side-drawer {
        display: none;
    }
}
.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.3);
    z-index: 100;
}
